.profile__author {
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  color: #ffffff;
  margin: 0;
  padding: 0 18px 0 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 420px) {
  .profile__author {
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    padding: 0 10px 0 0;
  }
}
